import React, { Component } from "react";
import styled from "styled-components";
import { Translation } from "react-i18next";

class News extends Component {
	constructor(props) {
		super(props);
		this.state = {
			news: [
				// {
				//   "imgUrl" : "https://s3.amazonaws.com/gallerist/products/4937/large/simple-nature.jpg?1496302338",
				//   "title" : "Traveling in China Winter horse riding",
				//   "date" :  "13-06-2019 Thursday",
				//   "author":  "In Travelling",
				//   "article": "Lorem ipsum dolorem dolog anet sobok bla bla bla bla bla dolorem pfasdasdasdasd Lorem ipsum dolorem dolog anet sobok bla bla bla bla bla dolorem pfasdasdasdasd Lorem ipsum dolorem dolog anet sobok bla bla bla bla bla dolorem pfasdasdasdasd Lorem ipsum dolorem dolog anet sobok bla bla bla bla bla dolorem pfasdasdasdasd"
				// }
			],
			direction: "flex-row-reverse"
		};
	}

	componentDidMount() {
		this.callNews();
	}

	callNews = () => {
		fetch("http://api.ukpandatv.com/api/get/news/", {
			method: "GET",
			headers: new Headers({
				"Content-Type": "text/plain",
				apikey: "be741c63-69b3-4c62-a209-526301e638ff"
			})
		})
			.then(response => response.json())
			.then(data => {
				console.log(data);
				data.Data.forEach(e => {
					if (e.image && !e.image.includes("http")) {
						e.image = "http://api.ukpandatv.com/img/" + e.image;
					}
				});
				this.setState({
					news: data.Data.reverse()
				});
			})
			.catch(error => this.setState({ error, isLoading: false }));
	};

	clickHandler = id => {
		this.props.history.push("/News/" + id);
		// this can be used to call individual page for video/radio/news
	};

	render() {
		const FullHeightCon = styled.div`
			width: 100vw;
			height: 80vh;
			padding: 0;
			margin: 0;
			overflow: auto;
		`;
		return (
			<Translation>
				{t => (
					<div className="main-page">
						<FullHeightCon className="main-banner">
							<div className="col-sm-12 newsBg panel justify-content-center align-items-center h-100 d-flex flex-column text-center m-0 p-0">
								<p className="text-white pageTitle text-uppercase">
									News
								</p>
							</div>
						</FullHeightCon>
						<div className="newsContainerMobile flex-column ">
							{this.state.news.map((element, i) => {
								return (
									<div
										key={i}
										className={
											"newsWrapper position-relative text-left  d-flex flex-column justify-content-center"
										}
									>
										<img
											className="newsImage"
											src={element.image}
											alt={element.title}
											onClick={() =>
												this.clickHandler(element.id)
											}
										/>

										<div
											className="d-flex flex-column justify-content-center"
											style={{
												zIndex: "2",
												width: "100%"
											}}
										>
											<div className={`newsDescription`}>
												<div
													className="tomatoText newsTitle"
													onClick={() =>
														this.clickHandler(
															element.id
														)
													}
												>
													{t(
														"news_title_" +
															element.id
													)}
												</div>
												<div className="tomatoText dateText">
													<div className="subtitle">
														<div className="category">
															{t(
																"category_name_" +
																	element.categoryID
															)}
														</div>
														<div>{`${new Date(
															element.createdAt
														)
															.toUTCString()
															.slice(
																0,
																-13
															)}`}</div>
													</div>
												</div>

												<div
													className="newsPreview"
													dangerouslySetInnerHTML={{
														__html:
															t(
																"news_content_" +
																	element.id
															)
																.replace(
																	/^(\r\n|\n)*|(\r\n|\n)*$/gi,
																	""
																)
																.replace(
																	/\r\n|\n/gi,
																	"<br>"
																)
																.replace(
																	/\r\n|\n/gi,
																	"<br/>"
																)
																.substring(
																	0,
																	100
																) +
															(t(
																"news_content_" +
																	element.id
															).length > 100
																? "..."
																: "")
													}}
												/>
											</div>
										</div>
									</div>
								);
							})}
						</div>
						<div className="newsContainer d-flex flex-column hidden-mobile">
							{this.state.news.map((element, i) => {
								return (
									<div
										key={i}
										className={`newsWrapper position-relative text-left ${
											i % 2 ? "row-reverse-margin" : "a"
										}`}
									>
										<div className="col-md-7 newImageWrapper d-flex justify-content-end align-items-center">
											<img
												className="newsImage"
												src={element.image}
												onClick={() =>
													this.clickHandler(
														element.id
													)
												}
											/>
										</div>
										<div
											className="col-md-5 d-flex align-items-center"
											style={{ zIndex: "2" }}
										>
											<div
												className={`newsDescription ${
													i % 2 ? "left-margin" : ""
												}`}
											>
												<div
													className="tomatoText newsTitle"
													onClick={() =>
														this.clickHandler(
															element.id
														)
													}
												>
													{t(
														"news_title_" +
															element.id
													)}
												</div>
												<div className="tomatoText dateText">
													<div className="subtitle">
														<div className="category">
															{t(
																"category_name_" +
																	element.categoryID
															)}
														</div>
														<div>{`${new Date(
															element.createdAt
														)
															.toUTCString()
															.slice(
																0,
																-13
															)}`}</div>
													</div>
												</div>

												<div
													dangerouslySetInnerHTML={{
														__html:
															t(
																"news_content_" +
																	element.id
															)
																.replace(
																	/^(\r\n|\n)*|(\r\n|\n)*$/gi,
																	""
																)
																.replace(
																	/\r\n|\n/gi,
																	"<br/>"
																)
																.substring(
																	0,
																	300
																) +
															(t(
																"news_content_" +
																	element.id
															).length > 300
																? "..."
																: "")
													}}
												/>
											</div>
										</div>
									</div>
								);
							})}
						</div>
					</div>
				)}
			</Translation>
		);
	}
}

export default News;
