import React, { Component, useState, useEffect } from "react";
import styled from "styled-components";
import uuid from "uuid";
import { Translation } from "react-i18next";

import Slider from "react-slick";

class Videos extends Component {
	constructor(props) {
		super(props);
		this.state = {
			videoSeries: [],
			videos: [],
			isLoading: true,
			error: "",
			showCats: [],
			cats: []
		};
		this.API_KEY = process.env.API_KEY;
	}
	componentDidMount() {
		window.addEventListener("resize", this.resize.bind(this));
		this.resize();
		this.callVideos();
		this.callVideoSeries();
		this.callCats();
	}
	resize = () => {
		let currentHideNav = window.innerWidth <= 600;
		if (currentHideNav !== this.state.hideNav) {
			this.setState({ hideNav: currentHideNav });
		}
	};
	clickHandler = (id, e) => {
		this.props.history.push("/videos/series/" + id);
	};
	clickHandler2 = (id, e) => {
		this.props.history.push("/videos/" + id);
	};
	callVideoSeries = () => {
		fetch("http://api.ukpandatv.com/api/get/videoSeries/", {
			method: "GET",
			headers: new Headers({
				"Content-Type": "text/plain",
				apikey: "be741c63-69b3-4c62-a209-526301e638ff"
			})
		})
			.then(response => response.json())
			.then(data => {
				this.setState({
					videoSeries: data.Data.reverse()
				});
				let showCats = [];
				this.state.videoSeries.forEach(video => {
					showCats.push(video.category);
				});
				this.setState({
					showCats: showCats
				});
			});
		// .catch(error => this.setState({ error, isLoading: false }));
	};
	callVideos = () => {
		fetch("http://api.ukpandatv.com/api/get/video/", {
			method: "GET",
			headers: new Headers({
				"Content-Type": "text/plain",
				apikey: "be741c63-69b3-4c62-a209-526301e638ff"
			})
		})
			.then(response => response.json())
			.then(data => {
				let filterArr = data.Data.filter(e => e.isChapter);
				this.setState({
					videos: filterArr.reverse()
				});
			});
	};

	callCats = () => {
		fetch("http://api.ukpandatv.com/api/get/category/", {
			method: "GET",
			headers: new Headers({
				"Content-Type": "text/plain",
				apikey: "be741c63-69b3-4c62-a209-526301e638ff"
			})
		})
			.then(response => response.json())
			.then(data => {
				let result = data.Data.map(e => {
					return e;
				});
				console.log("a3");
				this.setState({
					cats: result
				});
			});
		// .catch(error => this.setState({ error, isLoading: false }));
	};

	render() {
		let settings;
		if (this.state.hideNav) {
			settings = {
				dots: true,
				infinite: false,
				speed: 500,
				slidesToShow: 1,
				slidesToScroll: 1,
				arrows: false
			};
		} else {
			settings = {
				dots: true,
				infinite: false,
				speed: 500,
				slidesToShow: 4.5,
				slidesToScroll: 4,
				arrows: false
			};
		}

		const FullHeightCon = styled.div`
			width: 100vw;
			height: 80vh;
			padding: 0;
			margin: 0;
			overflow: hidden;
		`;
		return (
			<Translation>
				{t => (
					<div className="main-page">
						<FullHeightCon className="main-banner">
							<div className="col-sm-12 videoBg panel justify-content-center align-items-center h-100 d-flex flex-column text-center m-0 p-0">
								<h1 className="text-white pageTitle">Videos</h1>
								<p />
							</div>
						</FullHeightCon>
						{/* <FullHeightCon> */}
						<div className="video-wrapper">
							<div className="d-flex justify-content-start">
								{/* "2019 Latest" */}
								<div className="categories">
									<div
										key={uuid.v4()}
										className="inner-wrapper"
									>
										<div className="category-label-wrapper">
											<a
												key={uuid.v4()}
												className="category-label text-capitalize"
												value="2019 Latest"
												href="#"
											>
												2019 Latest
											</a>
										</div>

										<div className="collection d-flex row p-3 ">
											{/* <Carousel responsive={responsive}> */}
											<Slider {...settings}>
												{this.state.videos.map(
													(video, i) => {
														if (
															// video.createdAt >=
															// 1546300800
															true
														) {
															return (
																<div
																	className="video-card "
																	key={uuid.v4()}
																>
																	<div className="videocard card m-2 cardBlur  video-player-container">
																		<div
																			className="card-image"
																			onMouseDown={e =>
																				this.clickHandler2(
																					video.id,
																					e
																				)
																			}
																		>
																			<img
																				src={
																					video.thumbnailUrl
																				}
																				alt="img"
																				className="video-card-image"
																			/>
																		</div>
																		<div className="card-content">
																			<div className="card-title text-center">
																				<h5 className="pt-2 tomatoText video-card-title">
																					{t(
																						"video_title_" +
																							video.id
																					)}
																				</h5>

																				<h5 className="pt-2 tomatoText video-card-subtitle">
																					{video.esp >
																					1
																						? "(1-" +
																						  video.esp +
																						  "" +
																						  t(
																								"esp"
																						  ).toUpperCase() +
																						  ")"
																						: " (1 " +
																						  t(
																								"esp"
																						  ).toUpperCase() +
																						  ")"}
																				</h5>

																				{/* <p className="categoryClass">
																		{
																			video.category
																		}
																	</p>
																	<p className="dateClass">{`${new Date(
																		video.createdAt
																	)
																		.toUTCString()
																		.slice(
																			0,
																			-13
																		)}`}</p> */}
																			</div>
																		</div>
																	</div>
																</div>
															);
														}
													}
												)}
												{/* </Carousel> */}
											</Slider>
										</div>
									</div>
									{this.state.cats.map((category, i) => {
										if (
											this.state.showCats.indexOf(
												category.name
											) !== -1
										) {
											return (
												<div
													key={uuid.v4()}
													className="inner-wrapper"
												>
													<div className="category-label-wrapper">
														<a
															key={uuid.v4()}
															className=" category-label text-capitalize"
															value={t(
																"category_name_" +
																	category.id
															)}
														>
															{t(
																"category_name_" +
																	category.id
															)}
														</a>
													</div>
													<div className="collection d-flex row p-3">
														<Slider {...settings}>
															{this.state.videoSeries.map(
																(video, i) => {
																	if (
																		video.category ===
																		category.name
																	) {
																		return (
																			<div
																				className="video-card"
																				key={uuid.v4()}
																			>
																				<div className="videocard card m-2 cardBlur">
																					<div
																						className="card-image"
																						onMouseDown={() =>
																							this.clickHandler(
																								video.id
																							)
																						}
																					>
																						<img
																							src={
																								video.thumbnailUrl
																							}
																							alt="img"
																							className="video-card-image"
																						/>
																					</div>
																					<div className="card-content">
																						<div className="card-title text-center">
																							<h5 className="pt-2 tomatoText video-card-title">
																								{t(
																									"video_title_" +
																										video.id
																								)}
																							</h5>

																							<h5 className="pt-2 tomatoText video-card-subtitle">
																								{video.esp >
																								1
																									? "(1-" +
																									  video.esp +
																									  "" +
																									  t(
																											"esp"
																									  ).toUpperCase() +
																									  ")"
																									: " (1 " +
																									  t(
																											"esp"
																									  ).toUpperCase() +
																									  ")"}
																							</h5>
																						</div>
																					</div>
																				</div>
																			</div>
																		);
																	}
																}
															)}
														</Slider>
													</div>
												</div>
											);
										}
									})}
								</div>
							</div>
						</div>
						{/* </FullHeightCon> */}
					</div>
				)}
			</Translation>
		);
	}
}

export default Videos;
