import React, { Component } from "react";
import styled from "styled-components";
import uuid from "uuid";
import { Translation } from "react-i18next";

class Series extends Component {
	constructor(props) {
		super(props);
		this.state = {
			videos: [],
			isLoading: true,
			error: "",
			categories: []
		};
		this.API_KEY = process.env.API_KEY;
	}
	componentDidMount() {
		this.callVideos();
		console.log(this.state.videos);
	}
	clickHandler = id => {
		this.props.history.push("/Videos/" + id);
		// this can be used to call individual page for video/radio
	};
	callVideos = () => {
		fetch(
			`http://api.ukpandatv.com/api/get/videoSeries/${this.props.match.params.id}`,
			{
				method: "GET",
				headers: new Headers({
					"Content-Type": "text/plain",
					apikey: "be741c63-69b3-4c62-a209-526301e638ff"
				})
			}
		)
			.then(response => response.json())
			.then(data => {
				console.log(data);
				this.setState({
					videos: data.Data
				});
			})
			.catch(error => this.setState({ error, isLoading: false }));
	};

	render() {
		console.log();
		const FullHeightCon = styled.div`
			width: 100vw;
			height: 100vh;
			padding: 0;
			margin: 0;
			overflow: hidden;
		`;
		return (
			<Translation>
				{t => (
					<div className="main-page">
						<FullHeightCon>
							<div className="col-sm-12 videoBg panel justify-content-center align-items-center h-100 d-flex flex-column text-center m-0 p-0">
								<h1 className="text-white pageTitle">
									{this.state.videos[0]
										? //? this.state.videos[0]["seriesTitle"]
										  t(
												"video_title_" +
													this.props.match.params.id
										  )
										: " "}
								</h1>
								<h1 className="text-white pageTitle">
									{this.state.videos[0]
										? `(1 - ${this.state.videos.length} ${t(
												"esp"
										  ).toUpperCase()} )`
										: " "}
								</h1>
								<div className="pageDescription"></div>
							</div>
						</FullHeightCon>
						<div>
							<div className="video-wrapper">
								<div className="seriesDescription">
									{this.state.videos[0]
										? // ? this.state.videos[0][
										  // 		"seriesDescription"
										  //   ]
										  t(
												"video_description_" +
													this.props.match.params.id
										  )
										: " "}
								</div>
								<div className="d-flex justify-content-start">
									<div className="collection d-flex row p-3">
										{this.state.videos.map((video, i) => {
											return (
												<div
													className="col-md-3 video-card"
													key={uuid.v4()}
													onClick={() =>
														this.clickHandler(
															video.id
														)
													}
												>
													<div className="videocard card m-2 cardBlur">
														<div className="card-image">
															<img
																src={
																	video.thumbnailUrl
																}
																alt="img"
																className="video-card-image"
															/>
														</div>
														<div className="card-content">
															<div className="card-title text-center">
																<h5 className="pt-2 tomatoText video-card-title">
																	{t(
																		"video_title_" +
																			video.id
																	)}
																</h5>

																<h5 className="pt-2 tomatoText video-card-subtitle">
																	{"" +
																		t(
																			"esp"
																		).toUpperCase() +
																		" " +
																		video.chapter}
																</h5>
															</div>
														</div>
													</div>
												</div>
											);
										})}
									</div>
								</div>
							</div>
						</div>
					</div>
				)}
			</Translation>
		);
	}
}

export default Series;
