import React, { Component } from "react";
import styled from "styled-components";
import Footer from "./Home/Footer.js";
import SocialMedia from "./socialmedia.js";
import { Translation } from "react-i18next";

const FullHeightCon = styled.div`
	width: 100vw;
	height: 100vh;
	padding: 0;
	margin: 0;
	overflow: hidden;
`;

class SingleNews extends Component {
	constructor(props) {
		super(props);
		this.state = {
			news: {}
		};
	}
	componentDidMount() {
		this.callRadio();
		console.log(this.state.radio);
	}
	callRadio = () => {
		fetch(
			`http://api.ukpandatv.com/api/get/news/${this.props.match.params.id}`,
			{
				method: "GET",
				headers: new Headers({
					"Content-Type": "text/plain",
					apikey: "be741c63-69b3-4c62-a209-526301e638ff"
				})
			}
		)
			.then(response => response.json())
			.then(data => {
				console.log(data);

				if (data.Data.image && !data.Data.image.includes("http")) {
					data.Data.image =
						"http://api.ukpandatv.com/img/" + data.Data.image;
				}
				this.setState({
					news: data.Data
				});
				this.setState({
					url: data.Data.url
				});
			})
			.catch(error => this.setState({ error, isLoading: false }));
	};

	render() {
		return (
			<Translation>
				{t => (
					<div className="news-page">
						<FullHeightCon>
							<div
								className="col-sm-12 news-page-bg panel justify-content-center align-items-center h-100 d-flex flex-column text-center m-0 p-0"
								style={{
									backgroundImage: `url(${this.state.news.image})`
								}}
							>
								{/* <img src={this.state.news.image} /> */}
								<div className="title">
									{t("news_title_" + this.state.news.id)}
								</div>
							</div>
						</FullHeightCon>
						<div className="d-flex flex-column justify-content-center align-items-center m-0 p-0 ">
							<div className="subtitle">
								<h5 className="category">
									{t(
										"category_name_" +
											this.state.news.categoryID
									)}
								</h5>
								<h5>{`${new Date(this.state.news.createdAt)
									.toUTCString()
									.slice(0, -13)}`}</h5>
							</div>

							<div
								className="content"
								dangerouslySetInnerHTML={{
									__html: t(
										"news_content_" + this.state.news.id
									)
								}}
							/>
							<div className="space" />
							<div className="socialMediaBox">
								<h3>Share it</h3>
								<SocialMedia />
							</div>
						</div>
						<div className="space" />
						<Footer />
					</div>
				)}
			</Translation>
		);
	}
}

export default SingleNews;
