import React, { Component } from "react";
import styled from "styled-components";
import Footer from "./Home/Footer.js";
import SocialMedia from "./socialmedia.js";
import ReactPlayer from "react-player";
import { Translation } from "react-i18next";
import { Video } from "react-av-player";

const FullHeightCon = styled.div`
	width: 100vw;
	height: 100vh;
	padding: 0;
	margin: 0;
	overflow: hidden;
`;

class Player extends Component {
	constructor(props) {
		super(props);
		this.state = {
			video: {},
			url: "",
			source: []
		};
		this.handleSource = this.handleSource.bind(this);
	}
	componentDidMount() {
		this.callVideo();
		console.log(this.state.video);
	}
	handleSource = url => {
		this.setState({
			url: url
		});
	};

	getId = url => {
		var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
		var match = url.match(regExp);

		if (match && match[2].length == 11) {
			return match[2];
		} else {
			return "error";
		}
	};

	callVideo = () => {
		fetch(
			`http://api.ukpandatv.com/api/get/video/${this.props.match.params.id}`,
			{
				method: "GET",
				headers: new Headers({
					"Content-Type": "text/plain",
					apikey: "be741c63-69b3-4c62-a209-526301e638ff"
				})
			}
		)
			.then(response => response.json())
			.then(data => {
				console.log(data);
				this.setState({
					video: data.Data
				});
				let url;
				if (data.Data.url) {
					url = data.Data.url;
				} else if (data.Data.url_2) {
					url = data.Data.url_2;
				} else if (data.Data.url_3) {
					url = data.Data.url_3;
				}

				this.setState({
					url: url
				});

				let source = [];

				source.push(data.Data.url);
				if (data.Data.url_2 !== "" && data.Data.url_2 !== null) {
					source.push(data.Data.url_2);
				}

				if (data.Data.url_3 !== "" && data.Data.url_3 !== null) {
					source.push(data.Data.url_3);
				}
				this.setState({
					source: source
				});
			})
			.catch(error => this.setState({ error, isLoading: false }));
	};

	render() {
		return (
			<Translation>
				{t => (
					<div className="player-page">
						<FullHeightCon>
							{this.state.url.includes("youtube") ? (
								// <ReactPlayer
								// 	url={this.state.url}
								// 	height="100vh"
								// 	width="100vw"
								// 	playing
								// 	fileConfig={{
								// 		attributes: { controls: true }
								// 	}}
								// />
								<div className="player-wrapper">
									<Video
										type="Youtube"
										args={{
											path: this.getId(this.state.url)
										}}
									/>
								</div>
							) : this.state.url.includes("bilibili") ? (
								<div className="player-wrapper">
									<Video
										type="Bilibili"
										args={{
											aid: this.state.url
												.split("/")[4]
												.substr(2)
										}}
									/>
								</div>
							) : this.state.url.includes("qq") ? (
								<div className="player-wrapper">
									<Video
										type="Tencent"
										args={{
											vid: this.state.url
												.split("/")[5]
												.slice(0, -5)
										}}
									/>
								</div>
							) : (
								""
							)}

							{/* <ReactPlayer
								url={this.state.url}
								height="100vh"
								width="100vw"
								playing
								fileConfig={{ attributes: { controls: true } }}
							/> */}
							{/* <div className="player-wrapper">
								<Video
									type="Bilibili"
									args={{ aid: "4502205" }}
								/>
							</div> */}
							{/* <div className="player-wrapper">
								<Video
									type="Tencent"
									args={{ vid: "o0196h7rqaj" }}
								/>
							</div> */}

							{/* <div className="player-wrapper">
								<iframe
									src="//player.bilibili.com/player.html?aid=4502205&cid=7297708&page=1"
									scrolling="no"
									border="0"
									frameborder="no"
									framespacing="0"
									allowfullscreen="true"
									height="100%"
									width="100%"
								>
									{" "}
								</iframe>
							</div> */}

							{/* <div className="player-wrapper">
								<iframe
									frameborder="0"
									src="https://v.qq.com/txp/iframe/player.html?vid=o0196h7rqaj"
									allowFullScreen="true"
									height="100%"
									width="100%"
								></iframe>
							</div> */}
						</FullHeightCon>
						<div className="d-flex flex-column justify-content-center align-items-center m-0 p-0 ">
							<div className="d-flex justify-content-around">
								{this.state.source.map((url, i) => {
									if (this.state.url === url) {
										return (
											<div
												className="source-btn active"
												onClick={() =>
													this.handleSource(url)
												}
												key={i}
											>
												{t("url") + " "}
												{i - -1}
											</div>
										);
									} else {
										return (
											<div
												className="source-btn"
												onClick={() =>
													this.handleSource(url)
												}
												key={i}
											>
												{t("url") + " "}
												{i - -1}
											</div>
										);
									}
								})}
							</div>
							<h1 className="title">
								{t("video_title_" + this.state.video.id)}
							</h1>
							<div className="subtitle">
								<h5 className="category">
									{t(
										"category_name_" +
											this.state.video.categoryID
									)}
								</h5>
								<h5>{`${new Date(this.state.video.createdAt)
									.toUTCString()
									.slice(0, -13)}`}</h5>
							</div>

							<p className="description">
								{t("video_description_" + this.state.video.id)}
							</p>
							<div className="socialMediaBox">
								<h3>Share it</h3>
								<SocialMedia />
							</div>
						</div>
						<div className="space" />
						<Footer />
					</div>
				)}
			</Translation>
		);
	}
}

export default Player;
