import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom";
import "../Assets/css/navbar.css";
import i18n from "../i18n";
import { Translation } from "react-i18next";
import Logo from "../Assets/img/ukpandatvlogo.png";

class Navigation extends Component {
	// static propTypes = {
	// 	match: PropTypes.object.isRequired,
	// 	location: PropTypes.object.isRequired,
	// 	history: PropTypes.object.isRequired
	// };
	constructor(props) {
		super(props);
		this.state = {
			navbarVisiability: true
		};
	}
	componentDidMount() {
		let path = this.props.location.pathname;
		let pathArr = path.split("/");
		if (pathArr[1] === "videos" && !isNaN(Number(pathArr[2]))) {
			this.setState({
				navbarVisiability: false
			});
		}
	}
	handleLanguage = () => {
		var lang = localStorage.getItem("lang") || "en";
		if (lang === "en") {
			i18n.changeLanguage("zh");
			localStorage.setItem("lang", "zh");
		} else if (lang === "zh") {
			i18n.changeLanguage("en");
			localStorage.setItem("lang", "en");
		}
	};
	handleShow = () => {
		// this.setState({
		// 	navbarVisiability: true
		// });
		let path = this.props.location.pathname;
		let pathArr = path.split("/");
		if (pathArr[1] === "videos" && !isNaN(Number(pathArr[2]))) {
			this.setState({
				navbarVisiability: true
			});
		}
	};
	handleHide = () => {
		// this.setState({
		// 	navbarVisiability: false
		// });
		let path = this.props.location.pathname;
		let pathArr = path.split("/");
		if (pathArr[1] === "videos" && !isNaN(Number(pathArr[2]))) {
			this.setState({
				navbarVisiability: false
			});
		}
	};
	render() {
		return (
			<Translation>
				{t => (
					<nav
						id="nav_bar"
						className={`position-fixed dekstopNav navbar navbar-expand-lg position-absolute customNavigation justify-content-center ${
							this.state.navbarVisiability ? "shown" : "hidden"
						}`}
						style={{ width: "100%", top: "5%", zIndex: 3 }}
						onMouseOver={this.handleShow}
						onMouseLeave={this.handleHide}
					>
						<nav className="navbar navbar-expand-lg navbar-light bg-light navbar_show_on_mobile bg-white-mobile">
							<div
								className={`logoContainer showLogo`}
								id="logoContainer"
							>
								<img
									src={Logo}
									alt="UK PANDA TV"
									height="50px"
								/>
							</div>
							<button
								className="navbar-toggler"
								type="button"
								data-toggle="collapse"
								data-target="#navbarTogglerDemo02"
								aria-controls="navbarTogglerDemo02"
								aria-expanded="false"
								aria-label="Toggle navigation"
							>
								<span className="navbar-toggler-icon"></span>
							</button>

							<div
								className="collapse navbar-collapse"
								id="navbarTogglerDemo02"
							>
								<ul className="navbar-nav mr-auto mt-2 mt-lg-0">
									<li className="nav-item">
										<Link to={"/"} className="nav-link cap">
											{t("home")}
										</Link>
									</li>
									<li className="nav-item">
										<Link
											to={"/videos"}
											className="nav-link cap"
										>
											{t("videos")}
										</Link>
									</li>
									<li className="nav-item">
										<Link
											to={"/radio"}
											className="nav-link cap"
										>
											{t("radio")}
										</Link>
									</li>
									<li className="nav-item">
										<Link
											to={"/news"}
											className="nav-link cap"
										>
											{t("news")}
										</Link>
									</li>
									<li className="nav-item">
										<Link
											to={"/contact"}
											className="nav-link cap"
										>
											{t("contact")}
										</Link>
									</li>
									<li className="nav-item">
										<Link
											to={"/login"}
											className="nav-link cap"
										>
											{t("login")}
										</Link>
									</li>
									<li className="nav-item">
										<Link
											to={"/register"}
											className="nav-link cap"
										>
											{t("register")}
										</Link>
									</li>
									<li className="nav-item">
										<a
											href="#"
											onClick={this.handleLanguage}
											className=" pr-md-1 nav-link"
										>
											{t("switchLang")}
										</a>
									</li>
								</ul>
							</div>
						</nav>

						<div
							className="p-2 bg-white menuClass menuText d-flex align-items-center hidden-mobile"
							style={{ borderRadius: "12px" }}
						>
							<div
								className={`logoContainer showLogo`}
								id="logoContainer"
							>
								<img
									src={Logo}
									alt="UK PANDA TV"
									height="50px"
								/>
							</div>
							<Link to={"/"} className="tomatoText cap">
								{t("home")}
							</Link>
							<span className="tomatoText">&nbsp;|&nbsp;</span>
							<Link to={"/videos"} className="tomatoText cap">
								{t("videos")}
							</Link>
							<span className="tomatoText">&nbsp;|&nbsp;</span>
							<Link to={"/radio"} className="tomatoText cap">
								{t("radio")}
							</Link>
							<span className="tomatoText">&nbsp;|&nbsp;</span>
							<Link to={"/news"} className="tomatoText cap">
								{t("news")}
							</Link>
							<span className="tomatoText">&nbsp;|&nbsp;</span>

							<Link to={"/contact"} className="tomatoText cap">
								{t("contact")}
							</Link>
						</div>
						<div
							className="p-2 tomatoBg tomatoShadow menuClass menuText d-flex align-items-center hidden-mobile"
							style={{ borderRadius: "12px" }}
						>
							<a
								href="#"
								onClick={this.handleLanguage}
								className="text-white pr-md-1"
							>
								{t("switchLang")}
							</a>
							<span className="text-white">&nbsp;|&nbsp;</span>
							<Link to={"/login"} className="text-white">
								{t("login")}
							</Link>
							<span className="text-white">&nbsp;|&nbsp;</span>
							<Link to={"/register"} className="text-white">
								{t("register")}
							</Link>
						</div>
					</nav>
				)}
			</Translation>
		);
	}
}

export default withRouter(Navigation);
