import React, { Component } from "react";
import styled from "styled-components";
import Footer from "./Home/Footer.js";
import SocialMedia from "./socialmedia.js";
import { Translation } from "react-i18next";
import geolocation from "./IPGeolocationAPI.js"
import Async from 'react-async';


const FullHeightCon = styled.div`
	width: 100vw;
	height: 100vh;
	padding: 0;
	margin: 0;
	overflow: hidden;
`;

class Player extends Component {
	constructor(props) {
		super(props);
		this.state = {
			radio: {},
			url: "",
			source: [],
                        country:''
		};
		this.handleSource = this.handleSource.bind(this);
	}
	async componentDidMount() {
		this.callRadio();
                await this.checkIP();
                
		console.log(this.state.radio);
	}
        
        checkIP = async()=>{
            
            fetch("https://api.ipgeolocation.io/ipgeo?apiKey=7c76ec8ce3d247ff844c580abf70374f", {
			method: "GET",
			
		})
			.then(response => response.json())
			.then(data => {
//                            console.log(data);
                    this.setState({
                        //HK,TW,CN
					country: data.country_code2
				});
			});
            
            
            
          
        }
	handleSource = url => {
		this.setState({
			url: url
		});
	};
	callRadio = () => {
		fetch(
			`http://api.ukpandatv.com/api/get/radio/${this.props.match.params.id}`,
			{
				method: "GET",
				headers: new Headers({
					"Content-Type": "text/plain",
					apikey: "be741c63-69b3-4c62-a209-526301e638ff"
				})
			}
		)
			.then(response => response.json())
			.then(data => {
//				console.log(data);
                            
                            
				this.setState({
					radio: data.Data
				});
                                
                                if(this.state.country ==='HK' || this.state.country ==='TW' || this.state.country ==='CN' || this.state.country ==='MO' ){
                                
                                    this.setState({
					url: data.Data.url_3
				});
                               
//				let source = [];
//				source.push(data.Data.url);
//				if (data.Data.url_2 !== "" && data.Data.url_2 !== null) {
//					source.push(data.Data.url_2);
//				}
//
//				if (data.Data.url_3 !== "" && data.Data.url_3 !== null) {
//					source.push(data.Data.url_3);
//				}
//				this.setState({
//					source: source
//				});
                                }else{
                                     this.setState({
					url: data.Data.url
				});
                                }
				
                                 console.log(this.state);
			})
			.catch(error => this.setState({ error, isLoading: false }));
	};

	render() {
		return (
			<Translation>
				{t => (
					<div className="player-page">
						<FullHeightCon>
							<div className="col-sm-12 radioBg panel justify-content-center align-items-center h-100 d-flex flex-column text-center m-0 p-0">
								<iframe
									id="soundCloudPlayer"
									scrolling="no"
									frameBorder="no"
									allow="autoplay"
									src={this.state.url}
								/>
							</div>
						</FullHeightCon>
						<div className="d-flex flex-column justify-content-center align-items-center m-0 p-0 ">
							<div className="d-flex justify-content-around">
								{this.state.source.map((url, i) => {
									if (this.state.url === url) {
										return (
											<div
												className="source-btn active"
												onClick={() =>
													this.handleSource(url)
												}
												key={i}
											>
												{i - -1}
											</div>
										);
									} else {
										return (
											<div
												className="source-btn"
												onClick={() =>
													this.handleSource(url)
												}
												key={i}
											>
												{i - -1}
											</div>
										);
									}
								})}
							</div>
							<h1 className="title">
								{t("radio_title_" + this.state.radio.id)}
							</h1>
							<div className="subtitle">
								<h5 className="category">
									{t("category_name_" + this.state.radio.id)}
								</h5>
								<h5>{`${new Date(this.state.radio.createdAt)
									.toUTCString()
									.slice(0, -13)}`}</h5>
							</div>

							<p className="description">
								{t("radio_description_" + this.state.radio.id)}
							</p>
							<div className="socialMediaBox">
								<h3>Share it</h3>
								<SocialMedia url={window.location.href} />
							</div>
						</div>
						<div className="space" />
						<Footer />
					</div>
				)}
			</Translation>
		);
	}
}

export default Player;
